body {
  color: white;
  background-color: #32323a;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.5px;
}

.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  flex-direction: column;
}

.App > img {
  width: 250px;
  height: 200px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-Body {
  width: 90%;
  height: 90%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.first > a:link {
  color: white;
  text-decoration: none;
}

.first > a:visited {
  color: white;
}

.Filter-Menu {
  display: flex;
  flex-direction: row;
  gap: 1.5em;
  justify-content: center;
  align-items: center;
  margin-top: 3em;
  margin-bottom: 3em;
  list-style-type: none;
}

.Filter-Menu > li {
  list-style-type: none;
}

.Menu-Button {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.Menu-Button > label {
  margin-top: 0.5em;
  font-size: 0.7em;
}

.Menu-Icons {
  display: flex;
  font-size: 1.5em;
  background: #5c5b6d;
  width: 2.5em;
  height: 2.5em;
  border-radius: 8px;
  align-items: center;
  justify-content: center;
}

.Filter-Menu a:link {
  color:white;
  text-decoration: none;
}
.Filter-Menu a:visited {
  color:white;
}

.first {
  display: flex;
  flex-direction: row;
  gap: 1em;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.searchBar {
  margin: 2em 2em;
}

.categoryIcon {
  background-color: #5c5b6d;
  display: flex;
  width: 100px;
  height: 150px;
  border-radius: 8px;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.categoryIcon > h1 {
  letter-spacing: 0.5px;
  font-size: 0.8em;
  font-weight: 300;
  color: #f3f3ff;
}

.categoryIcon > img {
  width: 70px;
  height: 70px;
}

.ingredients {
  display: flex;
  flex-direction: row;
  gap: 1em;
}

.Drink-List {
  width: 100vw;
}

.Drink-List > a:link {
  color: white;
  text-decoration: none;
}

.Drink-List > a:visited {
  color: white;
}

.Drink-List-Item {
  width: 100%;
}

.Drink-List-Item > hr{
  width: 100vw;
  color: white;
  border-top: 0;
  border-right: 0;
  border-left: 0;
}

.Drink-List-DrinkData {
  display: flex;
  flex-direction: row;
  width: 80vw;
  margin: auto;
  gap: 2em;
  font-size: 1.4em;
}

.Drink-List-DrinkData > img{
  width: 150px;
  height: 200px;
}

 .Drink-Category {
  margin-top: 2em;
  color: #FFCB42;
  font-size: 0.5em;
  font-weight: 300;
  margin-bottom: 0;
}

.Drink-Text {
  margin-bottom: 0;
  margin-top: 0.2em;
}

.Drink-Card {
  display: flex;
  flex-direction: column;
  height: 90vh;
  width: 90vw;
  margin: auto;
}

.Drink-Card > img {
  align-self: center;
  height: 50vh;
  width: 100vw;
  margin-top: 0;
  margin-bottom: 1.5em;
}

.Back-Arrow {
  display: flex;
  position: absolute;
  font-size: 2em;
  background: #5c5b6d;
  width: 1.3em;
  height: 1.3em;
  border-radius: 8px;
  align-items: center;
  justify-content: center;
  margin-top: 5vh;
  opacity: 80%;
}

.Drink-Card > nav > a:link {
  color: white;
}

.Drink-Card > nav > a:visited {
  color: white;
}

.Drink-Text-Field {
  position: absolute;
  top: 47vh;
  left: 0;
  width: 100vw;
  height: 70vh;
  background-color: #404258;
  border-radius: 2em;
}

.Drink-Card > .Drink-Text-Field > .Drink-Category {
  margin-top: 3em;
  margin-left: 5vw;
  color: #FFCB42;
  font-size: 1em;
  font-weight: 300;
  margin-bottom: 0;
}

.Drink-Card > .Drink-Text-Field > .Drink-Name {
  margin-left: 5vw;
  font-size: 2em;
  font-weight: 400;
  margin-top: 0;
}

.Drink-Card > .Drink-Text-Field > .Drink-Label {
  margin-left: 5vw;
  letter-spacing: 0.5px;
  font-size: 1em;
  font-weight: 500;
  margin-bottom: 0;
}

.Drink-Card > .Drink-Text-Field > .Drink-Ingredients {
  margin-left: 5vw;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-basis: 27%;
  gap: 1em;
}

.Drink-Card > .Drink-Text-Field > .Drink-Ingredients-Text {
  margin-left: 5vw;
}

.Drink-Card > .Drink-Text-Field > .Drink-Instructions-Text {
  margin-left: 5vw;
  padding-bottom: 1em;
}